
import React,{ Component } from 'react';
import 'babel-polyfill';
import { Commen } from './Counter.js'
import { Testall } from './Test.js'
import { Skipall } from './Skip.js'
import { TemperatureContainer } from'./ParentAndChild'
import { Progress } from './Progress.js'
class App extends Component{
	constructor(props){
		super(props);
		this.state={
		   progress:'-'
		        
	    };
	}
	componentDidMount(){
		$("#player").jPlayer({
			ready:function(){
				$(this).jPlayer('setMedia',{
					mp3:'../assets/bootstrap/js/-.mp3'
				}).jPlayer('play');
			},
			supplied:'mp3',
			vmode:window
		})
		$("#player").bind($.jPlayer.event.timeupdate,(e)=>{
			this.setState({
				progress:Math.round(e.jPlayer.status.currentTime)
			})
		})
	}
	render() {
	console.log(this.props)	
    return (
    <div>
    	<div id="player"></div>
    	<Progress progress={this.props.progress}></Progress>
     	{this.props.children}
    </div>
   )
  }
}
export { App };


